import { Component, OnInit } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
@Component({
  selector: 'app-homepage',
  templateUrl: './homepage.component.html',
  styleUrls: ['./homepage.component.css']
})
export class HomepageComponent implements OnInit {
   user={
		penName:"好好地",
		serialNo:"1231123",
		allMoney:123,
		canUseMoney:10,
		leftovers:10,
		settled:20
	};
	// 声明一个数组，用于存储书籍信息
	bookList = [];
	notice_info="举动IE京东我见到我我偶尔觉得我哦";
	// 从接口获取书籍列表，可以直接使用xmlhttprequest
	getBookList(){
		let xhr = new XMLHttpRequest();
		xhr.onreadystatechange = () =>{
			if (xhr.readyState == 4) {
				this.bookList = JSON.parse(xhr.responseText).showapi_res_body.bookList;
				console.log(this.bookList);
				for (var i = 0; i < this.bookList.length; ++i) {
					// 由于原图片不能使用，此处统一设置
					this.bookList[i].img="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1506397436788&di=6fd08f449156b648c276366efd6f9b6c&imgtype=jpg&src=http%3A%2F%2Fimg0.imgtn.bdimg.com%2Fit%2Fu%3D723273428%2C3545501812%26fm%3D214%26gp%3D0.jpg";
				}

			}
		}
		xhr.open('get',"http://route.showapi.com/92-92?showapi_appid=45001&showapi_sign=82c0e7024f4f495f9f9949455b97003c&id=6");
		xhr.send(null);
	}
	// deleIndex = -1;
	// 定义删除书籍的方法
	deleteBook(index){
		// this.deleIndex = index;
         
		if (confirm("确定要删除此书吗？")) {
			this.bookList.splice(index,1);

		}
	}
  constructor() { }

  ngOnInit() {
  	this.getBookList();
  }

}



