import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-comment-manage',
  templateUrl: './comment-manage.component.html',
  styleUrls: ['./comment-manage.component.css']
})
export class CommentManageComponent implements OnInit {
  bookList = [];

    dataList=[
		{name:"十里送红军",type:"未签约",worldCount:123},
		{name:"十里送红军",type:"未签约",worldCount:123},
		{name:"十里送红军",type:"未签约",worldCount:123},
		{name:"十里送红军",type:"未签约",worldCount:123}
	]
   
    
     
      
    
  
  deleteBook(index){
     this.bookList=this.dataList;
    // this.deleIndex = index;
    console.log(this.bookList);
    if (confirm("确定要删除此书吗？")) {
      this.bookList.splice(index,1);


    }
  }
  constructor() { }

  ngOnInit() {
  }

}



